import { stopSubmit } from "redux-form";
import { authMe, logIn, logOut } from "../api/api";

const SET_USER_DATA = "auth/SET_USER_DATA";

let initialState = {
  id: null,
  email: null,
  login: null,
  isAuth: false,
};

const authReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_USER_DATA: {
      return {
        ...state,
        ...action.data,
      };
    }
    default:
      return state;
  }
};
export const setUserData = (id, login, email, isAuth) => {
  return { type: SET_USER_DATA, data: { id, login, email, isAuth } };
};
export const authMeThunkCreator = () => async (dispatch) => {
  let data = await authMe();
  if (data.resultCode === 0) {
    dispatch(setUserData(data.data.id, data.data.login, data.data.email, true));
  }
};

export const logInThunkCreator =
  (email, password, rememberMe) => async (dispatch) => {
    let data = await logIn(email, password, rememberMe);
    if (data.resultCode === 0) {
      dispatch(authMeThunkCreator());
    } else {
      let message =
        data.messages.length > 0 ? data.messages[0] : "Kakaya-to hueta happend";
      let action = stopSubmit("login", {
        _error: message,
      });
      dispatch(action);
    }
  };
export const logOutThunkCreator = () => async (dispatch) => {
  let data = await logOut();
  if (data.resultCode === 0) {
    dispatch(setUserData(null, null, null, false));
  }
};

export default authReducer;
